.BoxForm form {
  display: flex;
  flex-direction: column;
  width: 50%;
  margin: 0 auto;
  text-align: left;
  margin-top: 3rem;
}

.BoxForm--input_group {
    position: relative;
    min-height: 80px;
}

.BoxForm label {
  opacity: 0.75;
  color: #424242;
  font-size: 18px;
  position: absolute;
  top: 10px;
  left: 5px;
  transition: all 0.2s ease-in;
}


.BoxForm label.active {
  opacity: 0.9;
  font-size: 12px;
  position: absolute;
  top: -10px;
}

.BoxForm input[type="text"] {
  border: none;
  border-bottom: 1px solid #212121;
  height: 30px;
  font-size: 18px;
  padding: 5px;
  width: 100%;
}

.BoxForm input[type="text"]:focus {
  outline: none;
  border-bottom-color: #2A52BE;
}

.BoxForm--submit_btn {
  background: #2A52BE;
  color: #FFFFFF;
  font-size: 20px;
  border: none;
  text-transform: uppercase;
  width: auto;
  margin: 20px auto;
  padding: 10px 35px;
  border-radius: 29px;
  box-shadow: 2px 2px 8px rgba(33, 33, 33, 0.3);
  cursor: pointer;
  transition: all 0.2s ease-in;
}

.BoxForm--submit_btn:focus {
  outline: none;
}

.BoxForm--submit_btn:hover {
  box-shadow: 2px 2px 8px rgba(33, 33, 33, 0.5);
}

.BoxForm--heading_one {
  font-weight: 400;
}

.BoxForm--input_helper_text {
  font-size: 12px;
  margin-left: 5px;
  font-style: italic;
}
